ap;justify-content:center}.col{flex:0 0 auto;padding:0 12px;flex-grow:1;flex-basis:100%;max-width:100%}.col-1{max-width:8.33%}.col-2{max-width:16.66%}.col-3{max-width:25%}.col-4{max-width:33.33%}.col-5{max-width:41.66%}.col-6{max-width:50%}.col-7{max-width:58.33%}.col-8{max-width:66.66%}.col-9{max-width:75%}.col-10{max-width:83.33%}.col-11{max-width:91.66%}.col-12{max-width:100%}@media only screen and (max-width: 768px){.section-first-item{width:80%;margin:0 12px 0 12px}}@media (min-width: 768px){.section-first-item{order:0;max-width:33.33%;flex-basis:33.33%}}@media only screen and (max-width: 768px){.section-second-item{width:100%}}@media (min-width: 768px){.section-second-item{order:1;max-width:33.33%;flex-basis:33.33%}}.other-projects-photo{border-radius:3px;height:auto}@media (max-width: 768px){.other-projects-photo{width:80%}}@media (min-width: 768px){.other-projects-photo{width:100%}}

